.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.lang-icon {
  height: 50px;
}
.carousel-indicators.zindex-class {
    z-index: 1;
}
.pending-text{
  font-size: 25px;
  padding: 10px;
  color: #EB8153;
}
.pending-button{
  width: 150px;
}
.swal-footer{
  text-align: center;
}
.slider-1{
  transform: translate3d(0px, 0px, 0px); transition: all 0.25s ease 0s; width: 1667px;
}
.slider-2{
  width: 313.333px; margin-left: 20px;
}
.slider-3{
  width:90%; height:30px; border-radius:0
}
.slider-other{
  width: 313.333px; margin-left: 20px;
}
.selector {
  top: 21vh;
  right: 47.5vw;
  font-size: 50px;
  z-index: 3;
  color: #68cf29;
}
.timer-container{
  display: flex;
  align-content: center;
  justify-content: center;
}
.timer-box{
    height: 80px; width: 80px; text-align: center;border: 2px solid #51a6f5;border-radius: 50%;justify-content: center;display: flex;justify-content: center;align-content: center;align-items: center
}
.jconfirm .jconfirm-box {
  background: #242629 !important;
}
.jconfirm .jconfirm-box .jconfirm-buttons button.btn-default{
  background-color: #EB8153 !important;
  border-color: #EB8153 !important;
}
.jconfirm.jconfirm-white .jconfirm-box .jconfirm-buttons button.btn-default, .jconfirm.jconfirm-light .jconfirm-box .jconfirm-buttons button.btn-default{
  color: #fff !important;
}